import '../style2.css'

export default function ElPremio () {
    return(
        <div className="articles example">
            
 <h1><i>EL PREMIO</i></h1>
                    <p>Al abrir el candado encontrarán dentro de la caja una tarjeta
con el mensaje del juego: “Cooperando encontramos la mejor
forma de salir” Cada colegio podrá definir el “premio” o dar a
elegir a los estudiantes. 
<br/>
Algunas ideas son: <br/>

• Un desayuno/almuerzo/merienda cooperativa (dar la
posibilidad al grupo de organizar un encuentro con espíritu
cooperativo en donde se comparta una comida). 
<br/>
• Organizar una jornada de juegos cooperativos para la escuela,
la comunidad o familias, permitiendo que cuenten con un
espacio de tiempo semanal para organizar y diseñar esta
jornada. 
<br/>
• Pintar un mural con una imagen o frase sobre la cooperación. 
            </p>
        </div>
    )
        
    
}